function getTimerOnClickHandler(
  panelOptions: SierraPlotOptions,
  onOptionsChange: optionsChangeCallback
): React.MouseEventHandler<SVGElement> | undefined {
  var clickEventHandler: React.MouseEventHandler = (event) => {
    switch (panelOptions.transitionType) {
      case 'none':
        panelOptions.transitionType = 'groups';
        break;
      case 'groups':
        panelOptions.transitionType = 'charts';
        break;
      case 'charts':
        panelOptions.transitionType = 'none';
        break;
    }
    event.preventDefault();
    event.stopPropagation();
    onOptionsChange(panelOptions);
  };
  return clickEventHandler;
}

function getTimerOutlineColor(type: TransitionType) {
  return type === 'none' ? '#000000' : '#ffffff';
}

function getTimerLabel(panelOptions: SierraPlotOptions): string {
  switch (panelOptions.transitionType) {
    case 'none':
      return '';
    case 'charts':
      return 'C';
    case 'groups':
      return 'G';
  }
}
